export function CheckActionByRole(action: string) {
  const resourcies = JSON.parse(localStorage.getItem("resource_menu")) || [];
  const url = this.route.url.split("/") || [];
  const actionByUrl = resourcies.find((item) => {
    const paths = item.path.split("/") || [];
    return paths[1] === url[1] && paths[2] === url[2];
  });
  // console.log(actionByUrl);

  return actionByUrl
    ? actionByUrl.privileges.some((item) => {
        return item.eng === action && item.checked;
      })
    : false;
}

/**
 * @param {array} arr The array you want to filter for dublicates
 * @param {array<string>} indexedKeys The keys that form the compound key
 *     which is used to filter dublicates
 * @param {boolean} isPrioritizeFormer Set this to true, if you want to remove
 *     dublicates that occur later, false, if you want those to be removed
 *     that occur later.
 */
export function distinct(
  arr: any,
  indexedKeys: any,
  isPrioritizeFormer: boolean = true
) {
  const lookup = new Map();
  const makeIndex = (el) =>
    indexedKeys.reduce((index, key) => `${index}-${el[key]}`, "");
  arr.forEach((el) => {
    const index = makeIndex(el);
    if (lookup.has(index) && isPrioritizeFormer) {
      return;
    }
    lookup.set(index, el);
  });

  return Array.from(lookup.values());
}

