export function ConvertDateToUTC(date, showHour = true) {
  if (date) {
    const d = new Date(date);
    const dd = `${FormatTwoNum(d.getUTCDate())}/${FormatTwoNum(
      d.getUTCMonth() + 1
    )}/${FormatTwoNum(d.getUTCFullYear())}`;
    if (showHour) {
      return (
        dd +
        ` ${FormatTwoNum(d.getUTCHours())}:${FormatTwoNum(
          d.getUTCMinutes()
        )}:${FormatTwoNum(d.getUTCSeconds())}`
      );
    } else {
      return dd;
    }
  }
  return "";
}
export function DefaultDateControl() {
  const date = new Date();
  return {
    year: date.getFullYear(),
    month: date.getMonth() + 1,
    day: date.getDate(),
  };
}
