export function momentFormat(
  date,
  timeZone = "+00:00",
  formatString = "DD/MM/YYYY H:mm:ss"
) {
  return moment(date).zone(timeZone).format(formatString);
}
export function formatFromISO(date, formatString = "DD/MM/YYYY H:mm:ss") {
  return moment(date, moment.ISO_8601).zone("+00:00").format(formatString);
}

export function formatAmount(amount: any) {
  if (!amount) {
    return 0;
  }
  return amount.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ".");
}
export function makeId(length) {
  var result = "";
  var characters = "ABCDEFGHIJKLMNOPQRSTUVWXYZ0123456789";
  var charactersLength = characters.length;
  for (var i = 0; i < length; i++) {
    result += characters.charAt(Math.floor(Math.random() * charactersLength));
  }
  return result;
}
