import { Injectable } from '@angular/core';
import { Router } from '@angular/router';
import * as moment from 'moment';
import { ScopeAccessAgency } from 'src/app/shared/constants/constant';
@Injectable({
  providedIn: 'root'
})
export class AuthService {
  constructor(private route: Router) { }

  sendToken(token: string) {
    localStorage.setItem('LoggedInUser', token);
  }
  getToken() {
    return localStorage.getItem('LoggedInUser');
  }

  isLoggednIn() {
    // if (!moment().isBefore(this.getExpiration())) {
    //   this.logout();
    // }
    // return moment().isBefore(this.getExpiration());
    return this.getToken() !== null;
  }
  sendScopeAccess(scope: string) {
    localStorage.setItem('ScopeAccess', scope);
  }
  geScopeAccess() {
    let scope = false;
    if (localStorage.getItem('ScopeAccess') === 'SALE') {
      scope = false;
    } else {
      scope = true;
    }
    return scope;
  }
  sendODP(odp: string) {
    localStorage.setItem('ODP', odp);
  }
  getODP() {
    return localStorage.getItem('ODP');
  }
  isODP() {
    return this.getODP() !== null;
  }
  // lấy expire date của JWT
  getExpiration() {
    const token = this.decodeToken(localStorage.getItem('LoggedInUser'));
    if (token === null) {
      return moment().add(-10000, 'd').toDate();
    }
    return moment.unix(token.exp).toDate();
  }

  private decodeToken(token: string) {
    if (!token) {
      return null;
    }
    try {
      const base64Url = token.split('.')[1];
      const base64 = base64Url.replace('-', '+').replace('_', '/');
      return JSON.parse(window.atob(base64));
    } catch (e) {
      return null;
    }
  }

  logout() {
    localStorage.removeItem('LoggedInUser');
    localStorage.removeItem('UserProfile');
    localStorage.removeItem('RegisterPackage');
    localStorage.removeItem('ODP');
    localStorage.removeItem('resource_menu');
    localStorage.removeItem('config_manage');
    sessionStorage.removeItem('employeeShift');
    sessionStorage.removeItem('OrderInsert');
    // if (localStorage.getItem('ScopeAccess') === 'SALE') {
    //   this.myRoute.navigate(['sale', 'login']);
    // } else if (ScopeAccessAgency.includes(localStorage.getItem('ScopeAccess'))) {
    //   this.myRoute.navigate(['auth', 'login']);
    // } else {
    //   this.myRoute.navigate(['home']);
    // }
    this.route.navigate(['auth', 'login']);
    localStorage.removeItem('ScopeAccess');
  }
}
