import { HttpEvent, HttpHandler, HttpRequest } from '@angular/common/http';
import { Router } from '@angular/router';
import { Observable, throwError } from 'rxjs';
import { catchError } from 'rxjs/operators';

export class TokenInterceptor {
    constructor() { }

    intercept(req: HttpRequest<any>, next: HttpHandler): Observable<HttpEvent<any>> {
        if (req.url.includes('config.json')) {
            return next.handle(req);
        }
        const authToken = localStorage.getItem('LoggedInUser');
        // let authReq: HttpRequest<any>;
        // let forceRedirectOnFailure = false;

        if (authToken !== null) {
            req = req.clone({
                setHeaders: {
                    // don't required here, comment because  serve upload formdata
                    // 'Content-Type': 'application/json; charset=utf-8',
                    'Accept': 'application/json',
                    'Authorization': `${localStorage.getItem('LoggedInUser')}`
                }
            });
        }

        return next.handle(req).pipe(
            catchError(err => {
                // console.log(err);
                if ((err.status === 401)) {
                    this.redirectToLogin();
                    localStorage.clear();
                    sessionStorage.clear();
                }
                return throwError(err);
                // return Observable.throw(err);
            })
        ) as any;
    }

    redirectToLogin() {
        sessionStorage.setItem('CurrentUrl', location.href);
        location.href = '/auth/login';
        // if (localStorage.getItem('ScopeAccess') === 'SALE') {
        //     location.href = '/sale/login';
        // } else {
        //     location.href = '/auth/login';
        // }
    }
}
