import { BrowserModule } from "@angular/platform-browser";
import { NgModule, APP_INITIALIZER } from "@angular/core";

import {
  HttpClientModule,
  HttpClient,
  HTTP_INTERCEPTORS,
} from "@angular/common/http";
import { PagerService } from "./shared/components/pagination/pagination.service";

//---------Module--------///
import { AppRoutingModule, routes } from "./app-routing.module";
import { RouterModule } from "@angular/router";
import { SlimLoadingBarModule } from "ng2-slim-loading-bar";
import {
  PerfectScrollbarConfigInterface,
  PERFECT_SCROLLBAR_CONFIG,
  PerfectScrollbarModule,
} from "ngx-perfect-scrollbar";
import { NgbModule } from "@ng-bootstrap/ng-bootstrap";
// import { PageHeaderLeftModule } from "./shared/components/page-header-left/page-header-left.module";
//---------Module--------///

//---------Component--------///
import { AppComponent } from "./app.component";
import { BlankComponent } from "./layouts/blank/blank.component";
import { FullComponent } from "./layouts/full/full.component";
import { SpinnerComponent } from "./shared/spinner.component";
import { NavigationComponent } from "./shared/components/header-navigation/navigation.component";
import { SidebarComponent } from "./shared/components/sidebar/sidebar.component";
//---------Component--------///

// CONFIG
import { TokenInterceptor } from "./configs/token-interceptor";
import { AppConfigService } from "./configs/app-config.service";

export function loadConfigService(configService: AppConfigService): Function {
  return () => configService.load();
}
const DEFAULT_PERFECT_SCROLLBAR_CONFIG: PerfectScrollbarConfigInterface = {
  suppressScrollX: true,
  wheelSpeed: 1,
  wheelPropagation: true,
  minScrollbarLength: 20,
};

@NgModule({
  declarations: [
    AppComponent,
    BlankComponent,
    SpinnerComponent,
    FullComponent,
    NavigationComponent,
    SidebarComponent,
  ],
  imports: [
    BrowserModule,
    RouterModule.forRoot(routes, { enableTracing: false, useHash: false }),
    HttpClientModule,
    SlimLoadingBarModule.forRoot(),
    PerfectScrollbarModule,
    NgbModule,
  ],
  providers: [
    {
      provide: APP_INITIALIZER,
      useFactory: loadConfigService,
      deps: [AppConfigService],
      multi: true,
    },
    {
      provide: PERFECT_SCROLLBAR_CONFIG,
      useValue: DEFAULT_PERFECT_SCROLLBAR_CONFIG,
    },
    {
      provide: HTTP_INTERCEPTORS,
      useClass: TokenInterceptor,
      multi: true,
    },
    PagerService,
  ],
  bootstrap: [AppComponent],
})
export class AppModule {}
