import { Component, AfterViewInit, OnInit } from "@angular/core";
import { RouteInfo } from "./sidebar.metadata";
import { Router, ActivatedRoute } from "@angular/router";
import { NgbModal } from "@ng-bootstrap/ng-bootstrap";
// import { ResourceMenuService } from "../../service/resource-menu.service";
import { AuthService } from "../../../service/authorization/auth.service";
// import { distinct } from '../constants/constant';
// import { APP_CONFIG } from 'src/app/configs/app-config.service';
// import { AgencyManagementService } from 'src/app/agency/service/management.service';
import { UserService } from "src/app/service/user.service";

declare var $: any;

@Component({
  selector: "app-sidebar",
  templateUrl: "./sidebar.component.html",
})
export class SidebarComponent implements OnInit {
  showMenu = "";
  showSubMenu = "";
  public sidebarnavItems = [
    {
      path: "/service",
      title: "Dịch vụ tiện ích",
      icon: "icon-Car-Wheel",
      class: "",
      extralink: false,

      submenu: [
        {
          path: "/service/trading-topup",
          title: "Nạp tiền điện thoại",
          icon: "",
          class: "",
          extralink: false,
          submenu: [],
        },
        {
          path: "/service/order",
          title: "Mua mã thẻ",
          icon: "",
          class: "",
          extralink: false,
          submenu: [],
        },
        {
          path: "/service/recharge",
          title: "Đơn nạp tiền",
          icon: "",
          class: "",
          extralink: false,
          submenu: [],
        },
      ],
    },
  ];

  public fullName: any = "";
  public checkImportCard = false;
  public avatarUrl = "assets/images/no-user-icon.png";
  public userProfile: any;
  // this is for the open close

  constructor(
    private modalService: NgbModal,
    private route: Router,
    private activeRoute: ActivatedRoute,

    private auth: AuthService,
    private userService: UserService
  ) {}

  // End open close
  ngOnInit() {
    this.showMenu = "/service";
  }

  addExpandClass(element: any) {
    if (element === this.showMenu) {
      this.showMenu = "0";
    } else {
      this.showMenu = element;
    }
  }
  addActiveClass(element: any) {
    if (element === this.showSubMenu) {
      this.showSubMenu = "0";
    } else {
      this.showSubMenu = element;
    }
  }

  onMyProfile() {
    this.route.navigate(["agency", "profile"]);
  }
  onChangePassword() {
    this.route.navigate(["user", "change-password"]);
  }
  onLogout() {
    this.auth.logout();
  }
}
