import { Injectable } from "@angular/core";
import { HttpClient, HttpHeaders } from "@angular/common/http";
import { APP_CONFIG } from "src/app/configs/app-config.service";
import { Observable } from "rxjs";

@Injectable({
  providedIn: "root",
})
export class UserService {
  private httpOptions: any;
  private API_URL: string;
  private http: string;
  private httpGeneral: string;
  constructor(private httpService: HttpClient) {
    this.API_URL = `${APP_CONFIG.DNSRoot}`;
    this.httpOptions = {
      headers: new HttpHeaders({
        "Content-Type": "application/json",
        // Authorization:
        //   "eyJhbGciOiJIUzI1NiIsInR5cCI6IkpXVCJ9.eyJfaWQiOiI1ZTFkMzBiYzhkNGMzODFmNjllOTEzNzQiLCJpYXQiOjE1ODQzMzQwMjUsImV4cCI6MTU4NDM0ODQyNX0.R4JvL0tMK4PWoFyBdFB9PRQFIDY2KNor_Y0W-bGuk98"
      }),
    };
    this.http = location.origin + ":2303/api";
    const splitUrl = this.http.split(".");
    this.http = this.http.includes("localhost")
      ? `${splitUrl[0]}.mobilego.vn:2303/api`
      : this.http;

    this.httpGeneral = `${APP_CONFIG.System}`;
    // this.http = "mobilego.vn:2303/api";
  }

  login(loginInfo: any) {
    return this.httpService.post(
      `${this.http}/v1/auth/login`,
      loginInfo,
      this.httpOptions
    );
  }
  changePassword(user: any) {
    return this.httpService.post(
      `${this.API_URL}/user/change-password`,
      user,
      this.httpOptions
    );
  }
  resetPassword(user: any) {
    return this.httpService.post(
      `${this.http}/user/password/reset`,
      user,
      this.httpOptions
    );
  }

  // forgotPassword(user: any) {
  //   return this.httpService.post(`${this.API_URL}/user/forgot`, user, this.httpOptions);
  // }

  lockAndUnlockAccount(user: any) {
    return this.httpService.post(
      `${this.API_URL}/user/update/lock`,
      user,
      this.httpOptions
    );
  }
  insert(user: any) {
    return this.httpService.post(
      `${this.API_URL}/user/insert`,
      user,
      this.httpOptions
    );
  }
  update(user: any) {
    return this.httpService.post(
      `${this.API_URL}/user/update`,
      user,
      this.httpOptions
    );
  }
  deleted(userId: any) {
    return this.httpService.delete(
      `${this.API_URL}/user/deleted/${userId}`,
      this.httpOptions
    );
  }
  getAll() {
    return this.httpService.get(`${this.API_URL}/user/all`, this.httpOptions);
  }
  getDetail() {
    return this.httpService.get(
      `${this.API_URL}/user/detail`,
      this.httpOptions
    );
  }
  getDetailById(userId: any) {
    return this.httpService.get(
      `${this.API_URL}/user/detail/${userId}`,
      this.httpOptions
    );
  }
  getIdByAccountName(acc: any) {
    return this.httpService.post(
      `${this.http}/v1/agency/accounts/getIdByAccountName`,
      acc,
      this.httpOptions
    );
  }
  forgotPassword(acc: any, isAccount = true) {
    const name = isAccount ? "accounts" : "employee";
    return this.httpService.post(
      `${this.http}/v1/agency/${name}/forgotPassword`,
      acc,
      this.httpOptions
    );
  }
  sendSMS(sms: any) {
    return this.httpService.post(
      `${this.http}/v1/otp/sendSMS`,
      sms,
      this.httpOptions
    );
  }
  uploadFile(obj: FormData): Observable<any> {
    return this.httpService.post(
      `${this.http}/v1/agency/accounts/uploadAvatar`,
      obj
    );
  }
  getFile(obj) {
    return `${this.http}/v1/agency/accounts/getAvatar/${obj}`;
  }
  updateAgencyProfile(user: any) {
    return this.httpService.post(
      `${this.httpGeneral}/v1/agency/accounts/updateAgencyProfile`,
      user,
      this.httpOptions
    );
  }
  updatePersonalProfile(user: any) {
    return this.httpService.post(
      `${this.httpGeneral}/v1/agency/accounts/updatePersonalProfile`,
      user,
      this.httpOptions
    );
  }
  getEndingBalance(userId: any) {
    return this.httpService.get(
      `${this.http}/v1/agency/accounts/getEndingBalance/${userId}`,
      this.httpOptions
    );
  }

  // for sale
  checkShift(shift: any) {
    return this.httpService.post(
      `${this.httpGeneral}/v1/agency/shift/checkShift`,
      shift,
      this.httpOptions
    );
  }
}
