import { Component, AfterViewInit, EventEmitter, Output } from "@angular/core";
import {
  NgbModal,
  ModalDismissReasons,
  NgbPanelChangeEvent,
  NgbCarouselConfig,
} from "@ng-bootstrap/ng-bootstrap";
import { PerfectScrollbarConfigInterface } from "ngx-perfect-scrollbar";
import { AuthService } from "../../../service/authorization/auth.service";
import { Router } from "@angular/router";
import { formatAmount } from "../../constants/constant";
declare var $: any;

@Component({
  selector: "app-navigation",
  templateUrl: "./navigation.component.html",
})
export class NavigationComponent implements AfterViewInit {
  @Output() toggleSidebar = new EventEmitter<void>();

  public config: PerfectScrollbarConfigInterface = {};
  userProfile: any;
  avatarUrl = "/assets/images/no-user-icon.png";
  formatAmount = formatAmount;
  public showSearch = false;
  public fullName: any = "";

  constructor(
    private modalService: NgbModal,
    private route: Router,

    private auth: AuthService
  ) {}

  ngAfterViewInit() {}

  ngOnInit() {
    this.getProfile();
  }
  onChangePassword() {
    this.route.navigate(["user", "change-password"]);
  }
  onLogout() {
    this.auth.logout();
  }
  getProfile() {
    this.userProfile = JSON.parse(localStorage.getItem("UserProfile"));

    // this.fullName = this.userProfile.email;
    // if (this.userProfile.personal && this.userProfile.personal.avatar) {
    //   this.avatarUrl = this.userService.getFile(this.userProfile.personal.avatar);
    // }
  }
  onMyProfile() {
    this.route.navigate(["profile"]);
  }
}
