import { NgModule } from "@angular/core";
import { Routes, RouterModule } from "@angular/router";
import { BlankComponent } from "./layouts/blank/blank.component";
import { AuthenticationModule } from "./backend/authentication/authentication.module";
import { FullComponent } from "./layouts/full/full.component";

import { AuthGuard } from "./shared/auth-guard/auth.guard";

export const routes: Routes = [
  {
    path: "",
    component: FullComponent,
    children: [
      // {
      //   path: "",
      //   redirectTo: "shop/service",
      //   pathMatch: "full",

      //   canActivate: [AuthGuard],
      // },

      {
        path: "",

        loadChildren: () =>
          import("./shop/shop.module").then((m) => m.ShopModule),

        canActivate: [AuthGuard],
      },
      {
        path: "profile",

        loadChildren: () =>
          import("./profile/profile.module").then((m) => m.ProfileModule),

        canActivate: [AuthGuard],
      },
    ],
  },
  {
    path: "",
    component: BlankComponent,
    children: [
      {
        path: "",
        redirectTo: "auth",
        pathMatch: "full",
      },
      {
        path: "auth",

        loadChildren: () =>
          import("./backend/authentication/authentication.module").then(
            (m) => m.AuthenticationModule
          ),
        //   "./backend/authentication/authentication.module#AuthenticationModule",
      },
    ],
  },
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule],
})
export class AppRoutingModule {}
